@font-face {
  font-family: titlefirstslider;
  src: url("../Fonts/IRANSansWeb_Black.ttf");
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.titlefirstslider {
  font-family: titlefirstslider;
}
.swiper-button-next,
.swiper-button-prev {
  color: white;
  background-color: rgb(38, 37, 37);
  border-radius: 50%;
  width: 38px;
  height: 38px;
  font-weight: 900;
  font-size: 10px;
}
.swiper-button-prev:hover,
.swiper-button-next:hover {
  background-color: white;
  color: rgb(38, 37, 37);
}
